
@use '../base/variables' as v;

.alerta {
    font-family: sans-serif;
    padding: calc(v.$separacion / 4);
    color: darkgrey;
    width: 100%;
    display: block;
    margin-bottom: 2rem;
    text-align: center;
    font-weight: v.$bold;
    text-transform: uppercase;
    

    &.error {
        background-color: #b80f22;
    }
    &.exito {
        background-color: v.$verde;
    }
}